import React from 'react';
import { createStackNavigator } from '@react-navigation/stack';
import { ShowcaseIFrame } from '../components/showcaseIFrame.component';
import { ButtonAccessoriesShowcase } from '../components/button/buttonAccessories.component';
import { ButtonAppearancesShowcase } from '../components/button/buttonAppearances.component';

const { Navigator, Screen } = createStackNavigator();

const routes = [
  <Screen name='ButtonAccessories' component={() => ShowcaseIFrame(ButtonAccessoriesShowcase, 'ButtonAccessories')}/>,
  <Screen name='ButtonAppearances' component={() => ShowcaseIFrame(ButtonAppearancesShowcase, 'ButtonAccessories')}/>,
];

export const AppNavigator = <Navigator>{routes}</Navigator>;
